.copyright {
  display: flex;
  justify-content: center;
  align-items: center;

  @media (min-width: $tablet-width) {
    justify-content: flex-end;
  }
}

.copyright__text {
  @include font(19px, 700, 18px);
  margin-right: 30px;
}


.copyright__link {
  font-size: 0;
}

.copyright__image {
  fill: $seafoam-blue;

  &:hover {
    fill: $darkSeaGreen;
  }

  &:active {
    opacity: 0.3;
  }
}
