.page-header {
  min-height: 78px;

  @media (min-width: $tablet-width) {
    height: 90px;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    padding: 0 50px 0 50px;
  }

  @media (min-width: $desktop-width) {
    max-width: 1150px;
    align-items: flex-start;
    margin: 0 auto;
  }
}

.page-header__logo {
  display: flex;
  width: 100px;
  padding: 21px 5px 23px;
  margin: 0 auto;
  transition: opacity 0.3s ease;
  cursor: pointer;

  &:hover,
  &:focus {
    outline: none;
    opacity: 0.6;
  }

  &:active {
    opacity: 0.3;
  }

  @media (min-width: $tablet-width) {
    margin: 0;
    width: 115px;
    margin-top: -36px;
    padding: 0;
  }

  @media (min-width: $desktop-width) {
    padding: 10px 0;
    width: 150px;
    order: 1;
    margin: 0;
  }
}

.page-header__logo-image {
  width: 100%;
}
